<script lang="ts">
  import { createEventDispatcher } from "svelte";
  import FloatingButton from "./FloatingButton.svelte";
  import DeleteIcon from "./icons/DeleteIcon.svelte";

  export let video: Video;
  export let active: boolean;

  const dispatch = createEventDispatcher();

  function videoClicked() {
    window.open(video.url, "_blank");
  }

  function deleteVideo(_: Event) {
    dispatch("delete", video);
  }
</script>

<div class="playlist-video" class:is-active={active}>
  <img
    alt={video.title}
    src={video.thumbnailUrl}
    on:click|preventDefault={videoClicked}
  />
  <div class="video-details">
    <span class="video-title">{video.title}</span>
    <span>{video.channel}</span>
  </div>
  <div class="video-btns">
    <FloatingButton on:click={deleteVideo}><DeleteIcon /></FloatingButton>
  </div>
</div>

<style>
  .playlist-video {
    display: flex;
    padding: 0.5em 1em;
    cursor: pointer;
  }

  .playlist-video:hover {
    background-color: #eeeeee;
  }

  .playlist-video.is-active {
    background-color: #3273dc;
    color: #fff;
  }

  img {
    width: 120px;
    height: 65px;
    object-fit: cover;
  }

  .video-details {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0.5em;
    font-size: 16px;
  }

  .video-details > span {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .video-title {
    font-weight: bold;
  }

  .video-btns {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
  }
</style>
